/* -*- Mode: javascript; indent-tabs-mode: nil; c-basic-offset: 2 -*- */

(function() {
  'use strict';

  /*
   * sgTarget - Replace target for <a href> in DOM.
   * @memberof SOGo.Common
   * @restrict attribute
   * @param {object} sgTarget - the expression to process
   * @ngInject
   * @example:

   <div sg-target="part.content"><!-- msg --></div>
  */
  function sgTarget() {
    return {
      priority: 1,
      restrict: 'A',
      link: sgTargetLink
    };

    function sgTargetLink(scope, element, attrs) {
      console.log("sgTargetLink...");

      var ensureTargetRunsOnce = scope.$watch(
        function(scope) {
          // Watch the sg-target expression for changes
          return scope.$eval(attrs.sgTarget);
        },
        function(value) {
          
          var onAnchorClick = function (event) {
            if (this.href)
              window.open(this.href);
            if (event) {
              if (event.preventDefault)
                event.preventDefault(); // W3C DOM
              else
                event.returnValue = false; // IE
            }
          };
          //var anchors = angular.element(value)[0].getElementsByTagName('a');
          //for (var i = 0; i < anchors.length; i++) {
            //var anchor = angular.element(anchors[i]);
            // if (!anchor.href && anchor.readAttribute("moz-do-not-send")) {
            //   anchor.writeAttribute("moz-do-not-send", false);
            //   anchor.removeClassName("moz-txt-link-abbreviated");
            //   anchor.href = "mailto:" + anchors[i].innerHTML;
            // }
            // if (anchor.href.substring(0,7) == "mailto:") {
            //   anchor.observe("click", onEmailTo);
            //   if (typeof onEmailAddressClick == 'function')
            //     anchor.observe("contextmenu", onEmailAddressClick);
            //   anchor.writeAttribute("moz-do-not-send", false);
            // }
            // els
            //if (!anchor.id)
            //  anchor.observe("click", onAnchorClick);
          //}

          element.html(value);
          ensureTargetRunsOnce();
        }
      );
    }
  }

  angular
    .module('SOGo.Common')
    .directive('sgTarget', sgTarget);
})();
